
































import Vue from 'vue'
import { parseItem } from './utils/parser'
import LoadingView from './components/LoadingView.vue'
import ErrorView from './components/ErrorView.vue'
import SlideViewer from './components/SlideViewer.vue'
import SlideControl from './components/SlideControl.vue'
import { Item } from '@/types/item'
import { fetchItem } from './utils/http'

interface LocalState {
  isFetched: boolean
  isErrored: boolean
  item: Item | null
  page: number
  pages: string[]
  errorReason: string | null
}

export default Vue.extend({
  name: 'Kamishibai',
  props: {
    'data-item-id': String
  },
  data(): LocalState {
    return {
      isFetched: false,
      isErrored: false,
      item: null,
      page: 0,
      pages: [],
      errorReason: ''
    }
  },
  components: {
    SlideViewer,
    SlideControl,
    LoadingView,
    ErrorView
  },
  computed: {
    option() {
      return {
        version: '0.1.0'
      }
    }
  },
  methods: {
    handleMoveToPrev() {
      this.page = Math.max(0, this.page - 1)
    },
    handleMoveToNext() {
      this.page = Math.min(this.pages.length - 1, this.page + 1)
    }
  },
  async mounted() {
    if (!(this as any).dataItemId) {
      this.isErrored = true
      this.errorReason = '記事 ID が指定されていません。'
    }
    const link = document.createElement('link')
    link.rel = 'stylesheet'
    link.href =
      'https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css'
    this.$el.appendChild(link)
    try {
      const item = await fetchItem((this as any).dataItemId)
      this.item = item
      this.pages = parseItem(item)
      this.isFetched = true
    } catch (e) {
      this.isErrored = true
    }
  }
})
